/* You can add global styles to this file, and also import other style files */

/* Color Variables */
$white-color:#ffffff;
$blue-color:#031888;
$light-blue:#6096fd;
$black-color:#111111;
$paragraph-color:#646464;

/* Font-Family Variable */
$body-font:'Poppins';

body {
    color: $paragraph-color;
    font-family: $body-font;
    line-height: 1.8;
    padding: 0;
    margin: 0;
    font-size: 15px;
}
img {
    max-width: 100%;
}
a {
    text-decoration: none;
    display: inline-block;
    transition: .5s;

    &:hover {
        text-decoration: none;
    }
}
ul {
    li {
        display: inline-block;
    }
}
.d-table {
    width: 100%;
    height: 100%;
}
.d-table-cell {
    display: table-cell;
    vertical-align: middle;
}
p {
    color:$paragraph-color;
    line-height: 1.8;
}
i {
    line-height: 1;
}
h3 {
    font-size: 20px;
    font-weight: 600;
}
.form-control:focus, input:focus, button:focus, .btn:focus {
    outline: 0;
    box-shadow: 0 0 0 0 !important;
}
.ptb-100 {
    padding-top: 100px;
    padding-bottom: 100px;
}
.pt-100 {
    padding-top: 100px;
}
.pb-70 {
    padding-bottom: 70px;
}
.mr-20 {
    margin-right: 20px;
}
.pl-40 {
    padding-left: 40px;
}
.theme-button {
    .default-btn {
        font-size: 15px;
        color: $white-color;
        font-weight: 500;
        padding: 15px 35px;
        border-radius: 5px;
        margin-right: 25px;
        position: relative;
        text-transform: capitalize;
        border: 1px solid $white-color;
        z-index: 0;

        &::before {
            position: absolute;
            content: '';
            width:0;
            height:100%;
            left: 0;
            top: 0;
            z-index: -1;
            transition: .5s;
            border-radius: 5px;
            background: $light-blue;
        }
        &:hover::before {
            width: 100%;
            height: 100%;
        }
        &:hover {
            border:1px solid transparent;
        }
    }
}
.section-head {
    max-width: 600px;
    margin:-1px auto 55px;
    line-height: 1;

    span {
        font-size: 18px;
        font-weight: 500;
        color: #fb788e;
        margin-bottom:10px;
        display: inline-block;
    }
    h2 {
        font-size:40px;
        color: $black-color;
        font-weight: 600;
        margin-bottom:20px;
        line-height: 1;

        span {
            color: $light-blue;
            font-weight: 600;
            font-size:40px;
            margin-bottom: 0;
        }
    }
    p {
        margin-bottom: 0;
    }
}
.blue-title {
    h2 {
        span {
            color: $blue-color;
            font-size:40px;
            font-weight: 600;
        }
    }
}

/*------------ HOME PAGE ONE CSS START -------------*/
/* Header CSS Start */
.header-section {
    background: $blue-color;
    padding: 10px 0;

    .header-widget {
        ul {
            padding-left: 0;
            margin-bottom: 0;

            li {
                font-size: 14px;
                padding-right: 16px;
                margin-right: 10px;
                color:$white-color;
                position: relative;

                &::before {
                    position: absolute;
                    content: '';
                    background: $white-color;
                    width: 1px;
                    height:20px;
                    right: 0;
                    top: 3px;
                }
                i {
                    margin-right:5px;                
                    font-size: 18px;
                    top: 1px;
                    position: relative;
                }
                a {
                    color:$white-color;
                }
            }
            :nth-child(3) {
                &::before {
                    display: none;
                }
            }
        }
    }
    .header-social {
        ul {
            padding-left: 0;
            margin-bottom: 0;

            li {
                margin-left: 10px;

                a {
                    i {
                        color: $white-color;
                        background: #1d3094;
                        width: 30px;
                        height: 30px;
                        border-radius: 5px;
                        display: inline-block;
                        text-align: center;
                        line-height: 30px;
                        position: relative;
                        z-index: 0;
                        transition:.5s;

                        &::after {
                            position: absolute;
                            content: '';
                            width: 0;
                            height: 0;
                            background: $white-color;
                            top: 0;
                            left: 0;
                            border-radius: 5px;
                            z-index: -1;
                            transition: .5s;
                        }
                        &:hover::after {
                            width:100%;
                            height:100%;
                        }
                        &:hover {
                            color: $light-blue;
                        }
                    }
                }
            }
        }
    }
}
/* Header CSS End */

/* Navbar CSS Start */
.navbar-area {
    .main-nav {
        background: $white-color;
        padding:0;
        position: relative;
        transition:.5s;

        .navbar {
            padding:15px 0;

            .navbar-nav {
                .nav-item {
                    a {
                        color: #282828;
                        font-weight: 500;
                        text-transform: capitalize;

                        &:hover {
                            color: $blue-color;
                        }
                        &:hover i {
                            display: inline-block;
                            transform: rotate(90deg);
                        }
                        i {
                            top: 1px;
                            position: relative;
                            transition:.5s;
                        }
                    }
                    .active {
                        color: $blue-color !important;
                    }

                }
                .dropdown-toggle::after {
                    display: none;
                }
                .dropdown-menu {
                    background: $white-color;
                    transition:.5s;
                    border: 0;
                    box-shadow: 7px 5px 30px rgba(72,73,121,.15);
                    padding: 0;

                    .nav-item {
                        display: block;
                        padding:0;

                        a {
                            color: #282828;
                            border-bottom:1px dashed #ccb0b0;
                            position: relative;

                            &::before {
                                position: absolute;
                                content: '';
                                background: $light-blue;
                                width:3px;
                                height:0;
                                right: 0;
                                bottom: 0;
                                transition: .5s;
                            }
                            &::after {
                                position: absolute;
                                content: '';
                                background: $light-blue;
                                width:3px;
                                height:95%;
                                left: 0;
                                bottom: 0;
                                transition: .5s;
                            }
                            &:hover {
                                color: $blue-color;
                            }
                            &:hover::before {
                                height:95%;
                            }
                            &:hover::after {
                                height:0;
                            }
                            .active {
                                color: $blue-color;
                            }
                        }
                    }
                    :last-child a {
                        border-bottom: 0;
                    }
                }
            }
            .navbar-button {
                margin-left: 25px;

                a {
                    color: $white-color;
                    background: $light-blue;
                    padding: 10px 37px;
                    border-radius: 5px;
                    position: relative;
                    z-index: 0;
                    font-size: 16px;

                    &::before {
                        position: absolute;
                        content: '';
                        width: 100%;
                        height: 0;
                        background: $blue-color;
                        top: 0;
                        left: 0;
                        bottom: 0;
                        right: 0;
                        border-radius: 5px;
                        z-index: -1;
                        transition: .5s;
                    }
                    &:hover::before {
                        height:100%;
                    }
                }
            }
        }
    }
}
.is-sticky {
    position: fixed !important;
    top: 0 !important;
    left: 0;
    width: 100%;
    z-index: 999;
    box-shadow:0 2px 28px 0 rgba(0,0,0,.06);
    background: #ffffff !important;
    animation: 500ms ease-in-out 0s normal none 1 running fadeInDown; 
    transition: 0.6s;
}

/* Navbar CSS End */

/* Home CSS Start */
.home-slider-item  {
    background-position: center center;
    background-size: cover;
    height:100vh;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        background: linear-gradient(90deg, rgba(3,24,136,0.8463760504201681) 20%, rgba(0,0,0,0) 100%);
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }
}
.home-slider-area {
    position: relative;
}
.home-slider-area.owl-carousel .owl-nav button.owl-prev,
.home-slider-area.owl-carousel .owl-nav button.owl-next {
    position: absolute;
    left:25px;
    top:35%;
    width: 55px;
    height: 55px;
    border-radius: 30px;
    font-size:25px;
    color: $white-color;
    transition: .5s;
    background: rgba(0,0,0,0.35);

    &:hover {
        background: $light-blue;
    }
}
.home-slider-area.owl-carousel .owl-nav button.owl-next {
    left:unset;
    right:25px;
}
.home-slider-area .owl-dots {
    position: absolute;
    bottom:40px;
    left: 0;
    right: 0;
}
.home-slider-area.owl-theme .owl-dots .owl-dot.active span,
.home-slider-area.owl-theme .owl-dots .owl-dot:hover span {
    background:$white-color;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        width: 25px;
        height: 25px;
        left: -5px;
        right: 0px;
        top: -5px;
        border-radius:20px;
        background: transparent;
        border: 1px solid $white-color;
    }
}
.home-slider-area.owl-theme .owl-dots .owl-dot span {
    width: 15px;
    height: 15px;
    background: $light-blue;
}
.home-slider-area.owl-theme .owl-nav {
    margin-top: 0;
}
.items-bg1 {
    background-image: url(assets/img/home/1.png);
}
.items-bg2 {
    background-image: url(assets/img/home/2.png);
}
.items-bg3 {
    background-image: url(assets/img/home/3.png);
}
.home-text {
    max-width: 740px;
    z-index: 1;
    position: relative;

    h1 {
        font-size: 70px;
        font-weight: 700;
        margin-bottom: 40px;
        text-transform: capitalize;
        color:$white-color;
    }
    p {
        color: $white-color;
        margin-bottom: 50px; 
    }
    .active-btn {
        background: $light-blue;
        border:1px solid transparent;

        &::before {
            background: transparent;
        }
        &:hover {
            background: transparent;
            border: 1px solid $white-color;
        }
    }
}
/* Home CSS End */

/* Service CSS Start */
.service-section {
    position: relative;

    .service-shapes {
        img {
            position: absolute;
            animation: translate infinite 2s;
        }
        :nth-child(1) {
            top: 65px;
            left: 30%;
            animation-delay: .5s;
        }
        :nth-child(2) {
            top: 15%;
            left: 5%;
            animation-delay: .3s;
        }
        :nth-child(3) {
            right: 16%;
            top: 18%;
            animation-delay: .5s;
        }
        :nth-child(4) {
            left: 2%;
            top: 30%;
            animation-delay: .5s;
        }
        :nth-child(5) {
            top: 20%;
            right: 3%;
            animation-delay: .3s;
        }
        :nth-child(6) {
            top: 66%;
            left: 2%;
            animation-delay: .5s;
        }
        :nth-child(7) {
            right: 2%;
            top: 50%;
            animation-delay: .3s;
        }
        :nth-child(8) {
            bottom: 1%;
            left: 3%;
            animation-delay: .5s;
        }
        :nth-child(9) {
            bottom:25%;
            right:3%;
            animation-delay: .3s;
        }
        :nth-child(10) {
            bottom:10%;
            right:3%;
        }
    }
}
.service-card {
    text-align: center;
    border: 1px solid #c3ccff;
    border-radius:10px;
    padding: 35px 30px;
    transition: .6s;
    position: relative;
    z-index: 0;
    margin-bottom:30px;

    &:hover {
        box-shadow: 7px 5px 30px rgba(72,73,121,.15);
        transition: .6s ease-in-out;
    }
    &:hover::before {
        height: 100%;
    }
    &:hover::after {
        opacity:1;
    }
    &:hover i, &:hover h3, &:hover p {
        color: $white-color;
    }
    &:hover i::after {
        transform: scale(1);
    }
    &:hover .theme-button .default-btn {
        background: $white-color;
    }
    &::before {
        position: absolute;
        content: '';
        background: $light-blue;
        width: 100%;
        height:0;
        bottom: 0;
        left: 0;
        z-index: -1;
        border-radius: 10px;
        transition:.6s;
    }
    &::after {
        position: absolute;
        content: '';
        background-image: url(assets/img/service/shapes/service-pattern-1.png);
        width: 75px;
        height: 50px;
        top: 0;
        left: 0;
        right: 0;
        margin: auto;
        opacity: 0;
        transition:.6s;
    }
    i {
        color: $light-blue;
        font-size: 50px;
        display: inline-block;
        transition:.6s;
        position: relative;
        z-index: 0;
        margin-bottom: 20px;
    }
    h3 {
        color: $black-color;
        transition: .6s;
        margin-bottom:10px;
    }
    p {
        transition: .6s;
        margin-bottom:20px;
    }
    .theme-button {
        .default-btn {
            color: $light-blue;
            border: 1px solid #7ca4f1;
            margin-right: 0;
            padding: 10px 40px;

            &:hover::before {
                display: none;
                color: $light-blue;
            }
        }
    }
}
.active-service {
    box-shadow: 7px 5px 30px rgba(72,73,121,.15);

    &::before {
        height: 100%;
    }
    &::after {
        opacity:1;
    }
    i, h3, p {
        color: $white-color;
    }
    i::after {
        transform: scale(1);
    }
    .theme-button .default-btn {
        background: $white-color;
    }
}
/* Service CSS End */

/* Why Choose CSS Start */
.why-choose-section {
    z-index: 1;
    position: relative;
    padding: 100px 0 100px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background-color: $blue-color;
        opacity: 0.8;
    }
    .why-choose-shape {
        position: absolute;
        left:35%;
        top: 180px;
        animation: scale infinite 2s;
    }
}
.why-choose-bg {
    background-image: url(assets/img/why-choose/bg-1.png);
    background-position: center center;
    background-size: cover;
    position: relative;
    height: 100%;
}
.why-choose-img {
    background-image: url(assets/img/why-choose/why-choose1.png);
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 750px;
    max-width: 650px;
    bottom: 0;
    position: absolute;
}
.why-choose-text {
    .section-head {
        margin:0 0 35px;        

        h2 {
            color:$white-color;
            margin-bottom:30px;
            margin-top: -4px;
        }
        p {
            color: $white-color;
            padding-right: 90px;
        }
    }
}
.why-choose-accordian {
    margin-top: 30px;
    margin-right: 60px;

    .accordion {
        margin-bottom: 35px;

        .card {
            border-radius:5px;
            margin-bottom: 15px;
            border:0;

            .card-header {
                background: $white-color;
                border-bottom: 0;
                padding: 0;
                margin: 0;

                h2 {
                    a {
                        font-size: 16px;
                        color: $black-color;
                        font-weight: 500;
                        position: relative;
                        display: block;
                        text-align: left;
                        padding: 15px 20px;
                        line-height: 1;
                    }
                }
            }
            .card-body {
                color: #828282;
                font-size: 14px;
                padding: 10px 0 0 0;
                border-top: 1px solid #bebebe;
                margin:0px 20px 20px 20px;
            }
        }
    }
    .why-choose-contact {
        .form-control {
            font-size: 16px;
            color: $white-color;
            background:rgba(255,255,255,0.35);
            padding:12px 20px;
            border-radius: 5px;
            margin-right: 20px;
            float: left;
            margin-bottom: 15px;
            width: 278px;
            border: 0;
            height: 50px;
        }
        ::placeholder {
            color: $white-color;
        }
        button {
            font-size:16px;
            color: $white-color;
            background: $light-blue;
            padding:12px 35px;
            border-radius: 5px;
            margin-bottom: 15px;
            border: 1px solid transparent;
            height: 50px;
    
            &:hover {
                background: $blue-color;
                border: 1px solid $light-blue;
            }
        }
        p {
            font-size: 16px;
            color: $white-color;
            margin-bottom: -8px;
        }
    }
}
.why-choose-accordian .accordion .card-header a::after {
    position: absolute;
    content: 'x';
    font-size: 20px;
    color: #616161;
    font-weight: 500;
    right:20px;
    top: 11px;
}
.why-choose-accordian .accordion a.collapsed::after {
    position: absolute;
    content: '+';
    font-size: 25px;
    color: #616161;
    font-weight: 500;
    right:20px;
    top: 11px;
}
/* Why Choose CSS End */

/* Process CSS Start */
.process-section {
    padding-top: 160px;

    .process-card {
        text-align: center;
        position: relative;
        margin-bottom: 30px;
        padding: 20px 20px 0;

        &:hover i {
            color: $white-color;
        }
        &:hover i::after {
            background: $light-blue;
        }
        i {
            color: $light-blue;
            font-size:50px;
            position: relative;
            z-index: 0;
            margin-bottom:100px;
            display: inline-block;
            transition: .5s;

            &::after {
                position: absolute;
                content: '';
                width: 150px;
                height:150px;
                background: $white-color;
                border-radius: 5px;
                border: 1px dashed #adadad;
                left: -53px;
                top: -50px;
                z-index: -1;
                transform: rotate(45deg);
                box-shadow: 7px 5px 30px rgba(72,73,121,.15);
                transition: .5s;
            }
        }
        h3 {
            font-size: 22px;
            color: $black-color;
            margin-bottom: 13px;
        }
        p {
            margin-bottom: 0;
        }
        img {
            position: absolute;
            top: 28px;
            right: -35px;
            animation:fadeInLeft infinite 2s;
        }
    }
}
/* Process CSS End */

/* Price CSS Start */
.price-card-area {
    text-align: center;
    border: 1px solid #c3ccff;
    border-radius: 5px;
    transition:.6s;
    margin-bottom: 30px;

    &:hover .price-head-text::before {
        opacity:1;
    }
    &:hover .plan-btn a::before {
        width: 100%;
    }
    &:hover .plan-btn a {
        color: $white-color;
    }
    &:hover {
        box-shadow: 7px 5px 30px rgba(72,73,121,.15);
    }
    .price-head-text {
        background:$light-blue;
        border-radius: 5px 5px 0 0;
        position: relative;
        z-index: 0;
        padding: 20px;

        &::before {
            position: absolute;
            content: '';
            background-image: url(assets/img/price-shape.png);
            background-position: center center;
            background-size: cover;
            width:100%;
            height:100%;
            top: 0;
            left: 0;
            right:0;
            z-index: -1;
            transition:1s ease-in-out;
            opacity: 0;
        }
        h4 {
            font-size: 17px;
            color:$white-color;
            font-weight: 500;
            margin-bottom: 10px;
        }
        h2 {
            font-size: 30px;
            color: $white-color;
            font-weight: 500;
            margin-bottom: 0;

            span {
                font-size: 36px;
                color: $white-color;
                font-weight: 500;
            }
        }
    }
    .plan-features {
        padding:30px 30px 30px 90px;

        ul {
            text-align: left;
            margin-bottom: 0;
            padding-left: 0;

            li {
                display: block;
                font-size: 16px;
                font-weight: 500;
                color:$black-color;
                line-height: 1;
                margin-bottom:20px;

                i {
                    color: $blue-color;
                    margin-right: 5px;
                }
            }
            :last-child {
                margin-bottom: 0;
            }
        }
    }
    .plan-btn {
        margin-bottom: 30px;

        a {
            font-size: 18px;
            font-weight: 500;
            color: $blue-color;
            display: inline-block;
            border-radius: 5px;
            border: 1px solid $blue-color;
            padding:5px 30px;
            text-transform: capitalize;
            position: relative;

            &::before {
                position: absolute;
                content: '';
                width:0;
                height:100%;
                left: 0;
                top: 0;
                z-index: -1;
                transition:.5s ease-in-out;
                border-radius:3px;
                background: $blue-color;
            }
        }
    }
}
.active-price {
    box-shadow: 7px 5px 30px rgba(72,73,121,.15);

    .price-head-text::before {
        opacity:1;
    }
    .plan-btn a::before {
        width: 100%;
    }
    .plan-btn a {
        color: $white-color;
    }
}
/* Price CSS End */

/* Counter CSS Start */
.counter-section {
    z-index: 1;
    position: relative;
    padding-bottom: 100px;

    &::before {
        position: absolute;
        content: '';
        background:$blue-color;
        width: 100%;
        z-index: -1;
        height: 100%;
        top: 0;
        left: 0;
    }
    .offer-text {
        h2 {
            font-size: 40px;
            font-weight: 600;
            max-width: 405px;
            margin-bottom: 30px;
            color: $white-color;
            text-transform: capitalize;
            
            span {
                color: #fb788e;
            }
        }
        .theme-button {
            .default-btn {
                background: $white-color;
                color: $blue-color;
                border: 0;

                &:hover {
                    color: $white-color;
                }
            }
        }
    }
    .counter-area {
        background: $white-color;
        border-radius: 10px;
        text-align: center;
        position: relative;
        z-index:99;
        padding-top: 85px;
        padding-bottom:15px;
        box-shadow: 7px 5px 30px rgba(72,73,121,.15);

        &::before {
            position: absolute;
            content: '';
            background-image: url(assets/img/map.png);
            background-position: center center;
            background-size: cover;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            border-radius: 10px;
